/**
 * @author zhaohuai
 * id
 * 
 */
export const getCompanyInfoByGroupId = (groupId) => request('/H_roleplay-si/ds/getGroupById', {
  groupId: groupId
})
/**
 * @author zhaohuai
 * 
 */
export const saveCompanyInfo = (data) => {
  let {
    groupName,
    groupShortName,
    depFunction,
    status,
    comment,
    groupCode
  } = data
  return request('/H_roleplay-si/userInfo/saveGroup', {
    groupName: groupName,
    groupShortName: groupShortName,
    depFunction: depFunction,
    status: status,
    comment: comment,
    list: 'sys_group',
    groupType: 'C',
    parentId: '1',
    groupCode: groupCode
  }, 'POST');
};
/**
 * @author zhaohuai
 * 
 */
export const updateCompanyInfo = (data) => {
  let {
    groupName,
    groupShortName,
    depFunction,
    status,
    comment,
    groupCode,
    groupId
  } = data
  return request('/H_roleplay-si/userInfo/saveGroup', {
    groupName: groupName,
    groupShortName: groupShortName,
    depFunction: depFunction,
    status: status,
    comment: comment,
    list: 'sys_group',
    groupType: 'C',
    parentId: '1',
    groupCode: groupCode,
    groupId: groupId
  }, 'POST');
};
/**
 * @author zhaohuai
 * 
 * 
 */
export const getCompanyList = (groupId, target, currentPage, pageSize) => request('/H_roleplay-si/sysGroup/getCompanyList', {
  groupId: groupId,
  target: target,
  currentPage: currentPage,
  pageSize: pageSize
});
/**
 * 
 * @author zhaohuai
 * 
 */
export const updateHighCompany = (groupId, parentId) => {
  let data = [{
    groupId: groupId,
    parentId: parentId
  }];
  return request('/H_roleplay-si/sysGroup/update', {}, 'POST', data);
}
